<template>
  <div class="goodsadd">
    <Card>
      <div slot="title">商品添加</div>
      <div slot="content">
        <el-form
          ref="form"
          :model="goodsAddForm"
          label-width="80px"
          size="mini"
        >
          <el-form-item label="商品名称">
            <el-input v-model="goodsAddForm.name" class="goodsname"></el-input>
          </el-form-item>
          <el-form-item label="商品分类">
            <el-select
              v-model="goodsAddForm.typeName"
              placeholder="请选择添加商品"
            >
              <el-option
                v-for="item in typeArr"
                :key="item.cateName"
                :label="item.cateName"
                :value="item.cateName"
              ></el-option>
            </el-select>
          </el-form-item>
          <!--商品价格  -->
          <el-form-item label="商品价格">
            <el-input-number
              v-model="goodsAddForm.price"
              :min="1"
              label="价格"
            ></el-input-number>
          </el-form-item>
          <!-- 商品图片 -->
          <el-form-item label="上传图片">
            <el-upload
              class="avatar-uploader"
              action="http://127.0.0.1:5000/goods/goods_img_upload"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="baseUrl + imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <!-- 商品描述 -->
          <el-form-item label="商品描述">
            <el-input
              type="textarea"
              :rows="3"
              placeholder="请输入内容"
              v-model="goodsAddForm.textarea"
            >
            </el-input>
          </el-form-item>
          <!--  -->
          <el-form-item size="large">
            <el-button type="primary" @click="onSubmit">添加商品</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>
      </div>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/Card";
import { api_serTypeName, api_addGoods } from "@/api/goods";
export default {
  components: {
    Card,
  },
  data() {
    return {
      imageUrl: "", //图片局部路径
      baseUrl: "http://127.0.0.1:5000/upload/imgs/goods_img/", //包含http的路径
      goodsAddForm: {
        name: "",
        typeName: "",
        price: "",
        textarea: "",
      },
      typeArr: [], //分类名称
    };
  },
  methods: {
    async onSubmit() {
      let { name, typeName, price, textarea } = this.goodsAddForm;
      let { code } = await api_addGoods({
        name: name,
        category: typeName,
        price: price,
        imgUrl: this.imageUrl,
        goodsDesc: textarea,
      });
      if (code == 0) {
        this.$router.push("/goods/goods-list");
      }
    }, //添加提交按钮
    handleAvatarSuccess(res) {
      this.imageUrl = res.imgUrl;
    },
    beforeAvatarUpload(file) {},
  },
  async created() {
    let { categories } = await api_serTypeName();
    this.typeArr = categories;
  },
};
</script>

<style lang="less" scoped>
.goodsadd {
  background: #fff;
  user-select: none;
  border-radius: 4px;
  overflow: hidden;
  /deep/ h3 {
    border-bottom: 1px solid #eff1f7;
    padding: 10px;
  }
  /deep/ .el-form {
    padding: 10px 50px;
    .goodsname {
      width: 70%;
    }
    .el-upload {
      width: 120px;
      height: 120px;
      border: 1px solid#DCDFE6;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .avatar {
      width: 120px;
      height: 120px;
      display: block;
    }
    .el-textarea__inner {
      width: 70%;
    }
  }
}
</style>